import LoadingAnimation from "./LoadingAnimation";
import styles from "./Loading.module.css";
import React, { useEffect } from "react";

/* Time for loading screen */
const TIME = 3000;

const Loading = (props) => {
    const fetchAudio = async () => {
        const apiUrl = "https://aerialscrew.com/api/music" // todo: change url params w/ more models

        const res = await fetch(apiUrl, {"method": "GET","headers": {"Host": "aerialscrew.com"}})
        const audioBlob = await res.blob();
        const audioObjectURL = URL.createObjectURL(audioBlob);
        props.setAudioElem(new Audio(audioObjectURL));
        props.changeStage("play");
        // .then(response => response)
        // .then(response => {
        //   console.log("REQUEST",response);
        //   const audioBlob = await response.blob();
        //   const audioObjectURL = URL.createObjectURL(audioBlob);
        //   props.setAudioElem(new Audio(audioObjectURL));
        //   props.changeStage("play");
        // })
        // .catch(err => {
        //   console.log("Error");
        //   // console.log(response);
        //   console.log(err);
        // });
      };

    useEffect(()=>{
        fetchAudio()
    }, []);
  return (    
    <div className={styles.wrapper}>
        <LoadingAnimation className={styles.animation}/>
    </div>
  );
};
export default Loading;
