import React from 'react'
import { MusicVAE,Player } from '@magenta/music'
import Wrapper from '../UI/Wrapper';

const music_vae = () => {
    // models https://magenta.github.io/magenta-js/music/index.html
    // pre-trained: https://github.com/magenta/magenta-js/blob/master/music/checkpoints/README.md#table
    // https://storage.googleapis.com/magentadata/js/checkpoints/music_vae/trio_16bar
    const model = new MusicVAE('https://storage.googleapis.com/magentadata/js/checkpoints/music_vae/trio_16bar');

    const player = new Player();

    var color = "blue"

    const start = () => {
        console.log("color", color)
        color = "red"
        console.log("color", color)
        Player.tone.context.resume();
        
        model.initialize().then(
            // Endlessly sample and play back the result.
            function sampleAndPlay() {
              return model.sample(1)
                  .then((samples) => player.start(samples[0]))
                  .then(sampleAndPlay);
            })
    }
    
  return (
    <div>
        <Wrapper>
        music_vae
        <button onClick={start} style={{color: color}}>
        Start
        </button>
        </Wrapper>
    </div>
  )
}

export default music_vae